// Taken from https://daveceddia.com/multiple-environments-with-react/
let backendHost;
//const apiVersion = "v1";

const hostname = window && window.location && window.location.hostname;

console.log("Hostname is: " + hostname);
if (
  hostname === "etherclear.appspot.com" ||
  hostname === "prod-dot-default-dot-etherclear.appspot.com"
) {
  backendHost = "https://prod-dot-relayer-dot-etherclear.appspot.com";
} else if (hostname === "dev-dot-relayer-dot-etherclear.appspot.com") {
  backendHost = "https://dev-dot-relayer-dot-etherclear.appspot.com";
} else if (
  process.env.REACT_APP_USE_LOCAL_RELAYER === "true" &&
  (hostname === "localhost" || hostname === "127.0.0.1")
) {
  // The local relayer service is for debugging only. Users may run the app locally
  // with `npm run start`, in which case they should be using the prod environment
  backendHost = "http://localhost:8080";
} else {
  /*
else if (/^qa/.test(hostname)) {
  backendHost = `https://api.${hostname}`;
}
*/
  // Fall back to prod relayer service, even if user is on localhost.
  backendHost =
    process.env.REACT_APP_BACKEND_HOST ||
    "https://prod-dot-relayer-dot-etherclear.appspot.com";
}

//export const API_ROOT = `${backendHost}/api/${apiVersion}`;
export const API_ROOT = `${backendHost}/`;

// TODO: clean this up, don't need API_ROOT anymore
// We use the version to get the endpoint rather than
// the hostname because the user may be running the app locally,
// but still want to connect to the remote relayer service.
export const getEndpointForVersion = version => {
  if (backendHost === "http://localhost:8080") {
    return "http://localhost:8080/";
  }
  return "https://" + version + "-dot-relayer-dot-etherclear.appspot.com/";
};
