import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import { Paper, Tabs, Tab, Badge } from "@material-ui/core";
import { withStyles, createMuiTheme } from '@material-ui/core/styles';
import PhoneIcon from "@material-ui/icons/Phone";
import grey from "@material-ui/core/colors/grey";
import FavoriteIcon from "@material-ui/icons/Favorite";
import PersonPinIcon from "@material-ui/icons/PersonPin";


// custom mui theme
const theme = createMuiTheme();

const styles = {
  badgeStyles: {
    color: 'white',
  },
}
class MobileNavBar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      value: 0
    };

    this.tabClick = this.tabClick.bind(this);
  }

  // handles animation change on tabs
  handleChange = (event, value) => {
    this.setState({ value });
  };

  // handles redirects when tabs are clicked
  // eslint-disable-next-line
  tabClick = url => this.props.history.push(url);

  render() {
    // for counting notifications
    // eslint-disable-next-line
    const { appEvents, history, classes } = this.props;
    // TODO: this code is not DRY, need to make count its own prop
    // eslint-disable-next-line
    const eventCount =
      Object.keys(appEvents).length < 1 ? null : Object.keys(appEvents).length;

    /* eslint-disable */
    return (
      <Paper square>
        <Tabs
          value={this.state.value}
          onChange={this.handleChange}
          variant="fullWidth"
          style={{ backgroundColor: "#3b9eb5", paddingTop: "8px" }}
          indicatorColor="secondary"
          textColor={"primary"}
        >
          <Tab
            style={{ color: "white", paddingTop: "4px" }}
            label={
              <Badge badgeContent={eventCount} color="secondary" classes={{
                badge: classes.badgeStyles,
              }}>
                <p style={{ marginTop: '4px', marginRight: '6px' }}>App Events</p>
              </Badge>
            }
            onClick={() => this.tabClick("/app")}
          />
          <Tab
            style={{ color: "white" }}
            label={"My Payments"}
            onClick={() => this.tabClick("/app/payments")}
          />
        </Tabs>
      </Paper>
    );
    /** eslint-disable */
  }
}

export default withStyles(styles, { withTheme : theme })(withRouter(MobileNavBar));
