import React from "react";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import { createMuiTheme } from "@material-ui/core/styles";
import { withTheme } from "@material-ui/styles";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import Divider from "@material-ui/core/Divider";

// custom mui theme
const theme = createMuiTheme();

class CancelPaymentDialog extends React.Component {
  constructor(props) {
    super(props);
  }

  cancelPayment = () => {
    this.props.cancelPayment(this.props.payment.paymentId);
    this.props.toggleCancel(false)();
  };

  cancelPaymentFeeless = () => {
    this.props.cancelPaymentFeeless(this.props.payment.paymentId);
    this.props.toggleCancel(false)();
  };

  shortStr = (str, len) => {
    return str.substring(0, len) + "...";
  };
  render() {
    const key = this.props._key;
    return (
      <div>
        <Dialog
          open={this.props.open}
          onClose={this.props.toggleCancel(false)}
          aria-labelledby={"cancel-dialog-title" + key}
          aria-describedby={"cancel-dialog-description" + key}
        >
          <DialogTitle id={"cancel-dialog-title" + key}>
            {"Cancel payment " +
              this.shortStr(this.props.payment.paymentId, 10)}
          </DialogTitle>
          <DialogContent>
            <DialogContentText id={"cancel-dialog-description" + key}>
              This action will return funds back to you and close the payment.
            </DialogContentText>
          </DialogContent>
          <Divider />
          <DialogActions>
            <Button onClick={this.cancelPayment} color="primary">
              Confirm
            </Button>
            <Button onClick={this.cancelPaymentFeeless} color="primary">
              Confirm (Feeless)
            </Button>
            <Button
              onClick={this.props.toggleCancel(false)}
              color="primary"
              autoFocus
            >
              Go Back
            </Button>
          </DialogActions>
        </Dialog>
      </div>
    );
  }
}

export default withTheme(CancelPaymentDialog);
