import React from "react";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import { createMuiTheme } from "@material-ui/core/styles";
import { withTheme } from "@material-ui/styles";
import { withRouter } from "react-router-dom";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import Typography from "@material-ui/core/Typography";
import { CopyToClipboard } from "react-copy-to-clipboard";
import Tooltip from "@material-ui/core/Tooltip";
import IconButton from "@material-ui/core/IconButton";
import FileCopyIcon from "@material-ui/icons/FileCopy";
import Divider from "@material-ui/core/Divider";
import App from "./App";

const BigNumber = require("bignumber.js");

const toBN = num => {
  return new BigNumber(num);
};

const theme = createMuiTheme();

class SubmitDialog extends React.Component {
  state = {
    copied: false,
    paymentId: "None",
    // These are stored so that we only fetch a new paymentId
    // when one of them changes.
    prevCode: undefined,
    prevRecipient: undefined
  };

  constructor(props) {
    super(props);
  }

  handleSubmit = () => {
    let recipient =
      this.props.resolvedAddr === undefined
        ? this.props.newPayment.recipient
        : this.props.resolvedAddr;
    let codeHash = window.web3.utils.soliditySha3(this.props.code, recipient);
    this.props.createPayment(
      this.props.newPayment.amount,
      recipient,
      // TODO: not safe
      parseInt(JSONToMillis(this.props.newPayment.holdTime) / 1000),
      codeHash,
      this.props.sendEther ? this.props.denom : this.props.token,
      this.props.sendEther
    );
    this.props.toggleSubmit(false)();
    // forces redirect to app events section
    // TODO: update route when app events section route changes
    this.props.history.push("/app");
  };

  render() {
    // TODO: just pass in the newPayment here.
    let utils = window.web3.utils;
    let tokenAmountLabel = this.props.token;

    // TODO: defined twice
    const formatTimeJSON = jsonStr => {
      return (
        "Days: " +
        jsonStr["days"] +
        ", Hours: " +
        jsonStr["hours"] +
        ", Minutes: " +
        jsonStr["minutes"]
      );
    };

    // TODO: clean up, this is because of decimal values being entered in.
    // An error will be displayed, but this dialog is still rendered.
    let ethAmountLabel;
    if (this.props.sendEther) {
      try {
        ethAmountLabel =
          " (" +
          (this.props.newPayment.amount === ""
            ? "N/A"
            : utils
                .fromWei(
                  utils.toWei(this.props.newPayment.amount, this.props.denom),
                  "ether"
                )
                .toString()) +
          " ether)";
      } catch (error) {
        ethAmountLabel = "(N/A ether)";
      }
    }

    // Show only the last letter of each word.
    // Assumes that the words are all at least one letter.
    let obscuredPassphrase = (() => {
      let arr = this.props.code.split(" ");
      //let last = arr[arr.length-1];
      for (let i = 0; i < arr.length; i++) {
        arr[i] = "*".repeat(arr[i].length - 1) + arr[i][arr[i].length - 1];
      }
      return arr.join(" ");
    })();

    // We fetch the paymentId to present it to
    // the sender in case they want to copy it with
    // the passphrase.
    let handyInfo = {
      passphrase: this.props.code,
      shortId: this.state.paymentId
    };

    let recipientAddr =
      this.props.resolvedAddr === undefined
        ? this.props.newPayment.recipient
        : this.props.resolvedAddr;

    // TODO: this is a hack since the bytes -> uint isn't reversible.
    // May want to check for existence of ID as well here.
    if (this.props.etherclearContractInstance === undefined) {
      console.log("No Etherclear contract instance found");
    } else if (this.props.badAddr) {
      console.log("Bad address, will not retrieve paymentId");
    } else {
      let codeHash = window.web3.utils.soliditySha3(
        this.props.code,
        this.props.newPayment.recipient
      );

      // TODO: this causes a warning: "Cannot update during an existing state transition",
      // since we do a setState() call in this block.
      if (
        this.state.prevCode !== this.props.code ||
        this.state.prevRecipient !== this.props.recipient
      ) {
        this.props.etherclearContractInstance
          .getPaymentId(recipientAddr, codeHash)
          .then(result => {
            //result = window.web3.utils.numberToHex(result);
            console.log("Got paymentId for submit: " + result);
            if (
              this.state.paymentId !==
              result.toString().substring(0, 12) + "..."
            ) {
              this.setState({
                paymentId: result.toString().substring(0, 12) + "..."
              });
            }
          })
          .catch(error => {
            console.log("Error getting paymentId for submit: " + error);
          });

        this.setState({
          prevCode: this.props.code,
          prevRecipient: this.props.recipient
        });
      }
    }

    // Returns decimal token amounts (amount divided by pow(10,num_token_decimal_places))
    const displayPaymentFee = (sendEther, paymentFee, token) => {
      if (sendEther) {
        return window.web3.utils.fromWei(paymentFee.toString()) + " ETH";
      } else {
        let tokenInfo = this.props.getTokenMap()[token];

        return (
          paymentFee.div(toBN(10).pow(tokenInfo.decimals)).toString() +
          " " +
          token
        );
      }
    };

    let sendAmount = this.props.calculateSendAmount(
      this.props.sendEther,
      this.props.newPayment.amount,
      this.props.denom,
      this.props.token
    );

    let { paymentFee, baseFee } = this.props.calculateFees(sendAmount);

    let displayedPaymentFee = displayPaymentFee(
      this.props.sendEther,
      paymentFee,
      this.props.token
    );

    let displayedBaseFee =
      baseFee === undefined
        ? "0"
        : window.web3.utils.fromWei(baseFee.toString(), "ether");

    // Use of flex for displaying divs side by side:
    // https://stackoverflow.com/questions/10006175/display-two-divs-side-by-side
    return (
      <div>
        <Dialog
          open={this.props.open}
          onClose={this.props.toggleSubmit(false)}
          aria-labelledby="submit-dialog-title"
          aria-describedby="submit-dialog-description"
        >
          <DialogTitle id="submit-dialog-title">{"New Payment"}</DialogTitle>
          <Divider />
          <br />
          <DialogContent>
            <strong>Recipient:</strong>{" "}
            {
              <div style={{ height: "50px", overflow: "scroll" }}>
                {this.props.newPayment.recipient}
              </div>
            }
            <DialogContentText id="submit-dialog-description">
              <strong>Hold Time:</strong>{" "}
              {formatTimeJSON(this.props.newPayment.holdTime)}
              <br />
              <strong>Send amount:</strong> {this.props.newPayment.amount}{" "}
              {this.props.sendEther ? this.props.denom : ""}
              {this.props.sendEther ? ethAmountLabel : tokenAmountLabel}
              <br />
              <br />
            </DialogContentText>
            <div
              style={{
                display: "flex",
                flexDirection: "row",
                justifyContent: "spaceAround"
              }}
            >
              <div style={{ display: "flex", flexDirection: "column" }}>
                <strong>Passphrase:</strong> {obscuredPassphrase}
                <br />
                <strong>Short ID:</strong> {this.state.paymentId}
                <br />
              </div>
              <div
                style={{
                  display: "flex",
                  flexDirection: "column",
                  center: "vertical"
                }}
              >
                <CopyToClipboard
                  text={JSON.stringify(handyInfo)}
                  onCopy={() => this.setState({ copied: true })}
                >
                  <IconButton>
                    <Tooltip title={"Copy both to clipboard"}>
                      <FileCopyIcon />
                    </Tooltip>
                  </IconButton>
                </CopyToClipboard>{" "}
                {this.state.copied ? (
                  <span style={{ color: "red" }}>Copied.</span>
                ) : null}
              </div>
            </div>
            <DialogContentText>
              <br />
              <strong>Base Fee:</strong> {displayedBaseFee + " ETH"}
              <br />
              <strong>Payment Fee:</strong> {displayedPaymentFee}
              <br />
              <br />
              <strong>This passphrase is not saved.</strong> You must
              communicate this code to the recipient over a secure channel for
              them to be able to retrieve any funds.
              <br />
              <br />
              <strong>The payment is not yet confirmed.</strong> Clicking
              "Confirm" will open a notification or popup from the wallet
              provider. Follow the instructions there to finish signing and
              sending a payment.
              <br />
              <br />
              <strong>
                Token payments currently require two transactions.
              </strong>{" "}
              Wait for the first "Approve" call to succeed. When that happens, a
              request for the next transaction that transfers funds should
              appear, which must be completed to actually transfer tokens.
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button onClick={this.handleSubmit} color="primary">
              Confirm
            </Button>
            <Button
              onClick={this.props.toggleSubmit(false)}
              color="primary"
              autoFocus
            >
              Cancel
            </Button>
          </DialogActions>
        </Dialog>
      </div>
    );
  }
}

/**
 * @return {number}
 */
function JSONToMillis(obj) {
  let wrap = obj => {
    return obj === undefined ? 0 : obj;
  };
  let ms =
    1000 * parseInt(wrap(obj.seconds)) +
    60 * 1000 * parseInt(wrap(obj.minutes)) +
    60 * 60 * 1000 * parseInt(wrap(obj.hours)) +
    24 * 60 * 60 * 1000 * parseInt(wrap(obj.days));
  return ms;
}

export default withTheme(withRouter(SubmitDialog));
