/**
 * Payment Row component is a single
 * row of the Payments Index
 * It will hold no state and use the payment prop passed from
 * parent to display info about payment
 */
import React from "react";
import { withRouter } from "react-router-dom";
import { TableCell, TableRow } from "@material-ui/core";
import { createMuiTheme } from "@material-ui/core/styles";
import { withTheme } from "@material-ui/styles";
import MediaQuery from "react-responsive";
import App from "./App";

// custom mui theme
const theme = createMuiTheme();

const PaymentStatus = { OPEN: 0, CLOSED: 1, CANCELLED: 2 };

const PaymentRow = props => {
  const { payment, history } = props;
  let paymentStatusText;
  switch (parseInt(payment.state)) {
    case PaymentStatus.OPEN:
      paymentStatusText = "OPEN";
      break;
    case PaymentStatus.CLOSED:
      paymentStatusText = "CLOSED";
      break;
    case PaymentStatus.CANCELLED:
      paymentStatusText = "CANCELLED";
      break;
    default:
      paymentStatusText = "ERROR";
  }

  // TODO: new Date(0) is probably not a good way to fail here.
  const openDate =
    payment.paymentOpenTime === 0
      ? new Date(0)
      : new Date(payment.paymentOpenTime * 1000);

  const dateOptions = {
    year: "numeric",
    month: "short",
    day: "numeric",
    minute: "numeric",
    hour: "numeric"
  };

  let symbol = props.tokenAddrToSymbol()[payment.token.toLowerCase()];
  let numDecimals = 18;
  if (symbol !== "ETH") {
    numDecimals = parseInt(props.getTokenMap()[symbol].decimals);
  }

  let utils = window.web3.utils;

  let amount =
    symbol === "ETH"
      ? utils.fromWei(payment.sendAmount.toString(), "ether")
      : payment.sendAmount / Math.pow(10, numDecimals);

  return (
    <TableRow
      hover
      onClick={() =>
        history.push(`/app/payment/${payment.paymentId}`, { payment })
      }
    >
      <TableCell>{openDate.toLocaleString("en", dateOptions)}</TableCell>

      <MediaQuery query="(min-device-width: 1200px)">
        {matched => {
          if (matched) {
            return <TableCell>{amount}</TableCell>;
          } else {
            return "";
          }
        }}
      </MediaQuery>
      <TableCell>{symbol}</TableCell>
      <TableCell>{paymentStatusText}</TableCell>
    </TableRow>
  );
};

export default withTheme(withRouter(PaymentRow));
