import React from "react";
import PropTypes from "prop-types";
import { withStyles, createMuiTheme } from "@material-ui/core/styles";
import ExpansionPanel from "@material-ui/core/ExpansionPanel";
import ExpansionPanelDetails from "@material-ui/core/ExpansionPanelDetails";
import ExpansionPanelSummary from "@material-ui/core/ExpansionPanelSummary";
import ExpansionPanelActions from "@material-ui/core/ExpansionPanelActions";
import Typography from "@material-ui/core/Typography";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";

import Button from "@material-ui/core/Button";
import Divider from "@material-ui/core/Divider";
import LinearProgress from "@material-ui/core/LinearProgress";

// custom mui theme
const theme = createMuiTheme();

const styles = theme => ({
  root: {
    width: "80%",
    margin: "auto"
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    flexBasis: "33.33%",
    flexShrink: 0
  },
  secondaryHeading: {
    fontSize: theme.typography.pxToRem(15),
    color: theme.palette.text.secondary
  },
  icon: {
    verticalAlign: "bottom",
    height: 20,
    width: 20
  },
  details: {
    alignItems: "center"
  },
  helper: {
    borderLeft: `2px solid ${theme.palette.divider}`,
    padding: `${theme.spacing(1)}px ${theme.spacing(2)}px`
  },
  link: {
    color: theme.palette.primary.main,
    overflowX: "hidden",
    textDecoration: "none",
    "&:hover": {
      textDecoration: "underline"
    }
  }
});

//
class EventExpansionPanel extends React.Component {
  handleClearEvent = ts => () => {
    this.props.clearEvent(ts);
  };

  render() {
    const { classes } = this.props;

    const renderEvent = (index, event) => {
      let { ts, name, loading, statusText, msgLines } = event;
      return (
        <div key={ts} className={classes.root}>
          <ExpansionPanel style={{ overflowX: "hidden" }}>
            <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
              <div className={classes.heading}>
                <Typography>{name}</Typography>
              </div>
              <div className={classes.heading}>
                {loading ? <LinearProgress style={{ width: "50%" }} /> : ""}
              </div>
              <div className={classes.heading}>{statusText}</div>
            </ExpansionPanelSummary>
            <ExpansionPanelDetails className={classes.details}>
              <div>
                {msgLines.map((elem, index) => {
                  return typeof elem === "string" ? (
                    <div key={index} style={{ overflowX: "hidden" }}>
                      <p style={{ overflowX: "hidden" }}>{elem}</p>
                    </div>
                  ) : (
                    <div key={index}>{elem}</div>
                  );
                })}
              </div>
            </ExpansionPanelDetails>
            <Divider />
            <ExpansionPanelActions>
              <Button
                size="small"
                color="primary"
                onClick={this.handleClearEvent(ts)}
              >
                Clear
              </Button>
            </ExpansionPanelActions>
          </ExpansionPanel>
        </div>
      );
    };

    return (
      <div>
        {Object.keys(this.props.events).map((key, index) =>
          renderEvent(index, this.props.events[key])
        )}
      </div>
    );
  }
}

EventExpansionPanel.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles, { withTheme: theme })(EventExpansionPanel);
