// TODO: switch back to this once it's stable.
import { unstable_useMediaQuery as useMediaQuery } from "@material-ui/core/useMediaQuery";
import MediaQuery from "react-responsive";
import MuiThemeProvider from "@material-ui/core/styles/MuiThemeProvider";
import purple from "@material-ui/core/colors/purple";

const theme = createMuiTheme({
  palette: createPalette({
    primary: {
      light: "#757ce8",
      main: "#3c9eb5",
      dark: "#506184",
      contrastText: "#fff"
    },
    secondary: {
      light: "#ff7961",
      main: "#f44336",
      dark: "#ba000d",
      contrastText: "#000"
    }
  }),
  typography: {
    useNextVariants: true
  }
});

const styles = theme => ({
  "@global": {
    body: {
      backgroundColor: theme.palette.common.white
    }
  },
  appBar: {
    position: "relative",
    width: "100%",
    backgroundColor: "#3c9eb5"
  },
  toolbarTitle: {
    flex: 1
  },
  layout: {
    width: "auto",
    marginLeft: theme.spacing(3),
    marginRight: theme.spacing(3),
    [theme.breakpoints.up(900 + theme.spacing(3 * 2))]: {
      width: 900,
      marginLeft: "auto",
      marginRight: "auto"
    }
  },
  heroContent: {
    maxWidth: 800,
    margin: "0 auto",
    padding: `${theme.spacing(8)}px 0 ${theme.spacing(6)}px`
  },
  heroButtons: {
    marginTop: theme.spacing(4)
  },
  footer: {
    marginTop: theme.spacing(8),
    borderTop: `1px solid ${theme.palette.divider}`,
    padding: `${theme.spacing(6)}px 0`
  },
  icon: {
    marginRight: theme.spacing(2)
  }
});

const footers = [
  {
    title: "Company",
    description: ["About"]
  },
  {
    title: "Contact",
    description: [
      <a
        href={"https://github.com/etherclearapp/etherclear_frontend/issues"}
        style={{ color: "inherit" }}
        target={"_blank"}
      >
        Github Issues
      </a>
    ]
  },
  {
    title: "Resources",
    description: [
      <a
        href={
          "https://docs.google.com/document/d/1UEobbwmL78Yhy6vjY4yGEdYThFIiHOieujM5TfM1dp4/edit?usp=sharing"
        }
        style={{ color: "inherit" }}
        target={"_blank"}
      >
        Guide
      </a>,
      <Link to={"/faq"} style={{ color: "inherit" }}>
        FAQ
      </Link>,
      <a href="https://github.com/etherclearapp" style={{ color: "inherit" }}>
        Github repo
      </a>
    ]
  },
  {
    title: "Legal",
    description: ["Disclaimer", "Terms of use"]
  }
];

// The Homepage is the main page for the site, and uses a different
// (and simpler) menu.
class Homepage extends React.Component {
  state = {
    anchorEl: null
  };

  handleMenuIconClick = event => {
    this.setState({ anchorEl: event.currentTarget });
  };

  handleMenuIconClose = event => {
    this.setState({ anchorEl: null });
  };

  render() {
    const { classes } = this.props;
    // This means the user is on a mobile device.
    const { anchorEl } = this.state;
    //const displayMenuIcon = useMediaQuery("(max-device-width: 1224px)");

    const renderMenuIconMenu = () => {
      return (
        <React.Fragment>
          <Button onClick={this.handleMenuIconClick}>
            <MenuIcon
              aria-owns={anchorEl ? "simple-menu" : undefined}
              aria-haspopup="true"
            />
          </Button>
          <Menu
            id="simple-menu"
            anchorEl={anchorEl}
            open={Boolean(anchorEl)}
            onClose={this.handleMenuIconClose}
          >
            <MenuItem onClick={this.handleMenuIconClose}>
              <Link
                to={"/"}
                style={{
                  textDecoration: "none",
                  color: "inherit"
                }}
              >
                Home
              </Link>
            </MenuItem>
            <MenuItem onClick={this.handleMenuIconClose}>
              <a
                href={
                  "https://docs.google.com/document/d/1UEobbwmL78Yhy6vjY4yGEdYThFIiHOieujM5TfM1dp4/edit?usp=sharing"
                }
                style={{ textDecoration: "none", color: "inherit" }}
                target={"_blank"}
              >
                Guide
              </a>
            </MenuItem>
            <MenuItem>
              <Link
                to={"/faq"}
                style={{ textDecoration: "none", color: "inherit" }}
              >
                FAQ
              </Link>
            </MenuItem>
            <MenuItem>
              <Link
                to={"/app"}
                style={{ color: "#506184", textDecoration: "none" }}
              >
                App
              </Link>
            </MenuItem>
          </Menu>
        </React.Fragment>
      );
    };

    const buttonTheme = createMuiTheme({
      palette: createPalette({
        primary: {
          light: "#757ce8",
          main: "#b3fffd",
          dark: "#506184",
          contrastText: "#fff"
        }
      })
    });

    const renderRegularMenu = () => {
      return (
        <React.Fragment>
          <Link to={"/"} style={{ textDecoration: "none", color: "#fff" }}>
            <Button>
              <div style={{ color: "#fff" }}>Home</div>
            </Button>
          </Link>
          <a
            href={
              "https://docs.google.com/document/d/1UEobbwmL78Yhy6vjY4yGEdYThFIiHOieujM5TfM1dp4/edit?usp=sharing"
            }
            style={{ textDecoration: "none", color: "inherit" }}
            target={"_blank"}
          >
            <Button>
              <div style={{ color: "#fff" }}>Guide</div>
            </Button>
          </a>
          <Link to={"/faq"} style={{ textDecoration: "none" }}>
            <Button>
              <div style={{ color: "#fff" }}>FAQ</div>
            </Button>
          </Link>
          <MuiThemeProvider theme={buttonTheme}>
            <Link to={"/app"} style={{ textDecoration: "none" }}>
              <Button color="primary" variant="outlined">
                App
              </Button>
            </Link>
          </MuiThemeProvider>
        </React.Fragment>
      );
    };

    // See media query examples at https://github.com/contra/react-responsive
    return (
      <MuiThemeProvider theme={theme}>
        <React.Fragment>
          <CssBaseline />
          <AppBar position="static" color="default" className={classes.appBar}>
            <Toolbar>
              <Link
                className={classes.toolbarTitle}
                to={"/"}
                style={{ textDecoration: "none", color: "inherit" }}
              >
                <img src={require("./text-logo.png")} alt="Home" />
              </Link>

              <MediaQuery query="(max-width: 1224px), (max-device-width: 1224px)">
                {renderMenuIconMenu()}
              </MediaQuery>
              <MediaQuery query="(min-device-width: 1224px) and (min-width: 1224px)">
                {renderRegularMenu()}
              </MediaQuery>
            </Toolbar>
          </AppBar>
          <Switch>
            <Route exact path="/" component={HomeContent(this.props)} />
            <Route path="/faq" component={FAQContent(this.props)} />
          </Switch>
          {/* Footer */}
          <footer className={classNames(classes.footer, classes.layout)}>
            <Grid container spacing={4} justify="space-evenly">
              {footers.map((footer, ind) => (
                <Grid item xs key={ind}>
                  <Typography variant="h6" color="textPrimary" gutterBottom>
                    {footer.title}
                  </Typography>
                  {footer.description.map((item, ind) => (
                    <Typography
                      key={ind}
                      variant="subtitle1"
                      color="textSecondary"
                    >
                      {item}
                    </Typography>
                  ))}
                </Grid>
              ))}
            </Grid>
          </footer>
          {/* End footer */}
          <Typography align={"center"}>
            Email: etherclearapp (at) protonmail.com
          </Typography>
          <Typography
            variant="h6"
            color="inherit"
            align={"center"}
            noWrap
            style={{ paddingTop: "50px" }}
            className={classes.toolbarTitle}
          >
            Copyright  2019 Etherclear.{" "}
            <MediaQuery query={"(max-width: 1224px)"}>
              <br />
            </MediaQuery>
            All rights reserved.
          </Typography>
        </React.Fragment>
      </MuiThemeProvider>
    );
  }
}

Homepage.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(Homepage);
