import React from "react";
import { ListItem, List, Badge, Drawer } from "@material-ui/core";
import { withRouter } from "react-router-dom";
import { makeStyles, useTheme } from '@material-ui/styles';
import { Link } from "react-router-dom";





  // custom styles for MUI Components
const useStyles = makeStyles( theme => ({
  paper: {
      background: theme.palette.primary.main,
    },
  badgeStyles: {
    color: 'white',
    zIndex: '1',
  },
  badgeSpacing: {
    marginLeft: '32px',
  },
  }));

const SideNavBar = props => {
  const { appEvents } = props;
  // (we have to have this theme var but its unused????)
  // eslint-disable-next-line
  const theme = useTheme();
  // use custom MUI styles with theme passed throught
  const classes = useStyles();
  const eventCount =
    Object.keys(appEvents).length < 1 ? null : Object.keys(appEvents).length;

  return (
    <Drawer
      variant="permanent"
      anchor="left"
      open
      className={classes.paper}
      classes={{ paper: classes.paper }}
    >
      <div style={{ marginBottom: "32px", marginTop: "24px" }}>
        <Link to={"/"}>
          <img src={require("./text-logo.png")} alt="Etherclear Logo" />
        </Link>
      </div>
      <div>
        <List>
          {[
            ["My Payments", "/app/payments"],
            ["App Events", "/app/"],
          ].map((navItem, index) => (
            <div key={index}>
              {navItem[0] === "App Events" ? (
                <Link to={navItem[1]} style={{ textDecoration: "none" }}>
                  <ListItem button key={index}>
                    <Badge badgeContent={eventCount} classes={{
                      badge: classes.badgeStyles,
                    }} color="secondary">
                      <p
                        style={{
                          color: "white",
                          marginTop: '4px',
                          fontSize: '1.2em',
                          fontWeight: '400',
                          marginRight: '6px',
                          textDecoration:
                            props.location.pathname === navItem[1]
                              ? "underline"
                              : "none"
                        }}
                      >
                        {navItem[0]}
                      </p>
                    </Badge>
                  </ListItem>
                </Link>
              ) : (
                <Link to={navItem[1]} style={{ textDecoration: "none" }}>
                  <ListItem button key={index}>
                    <p
                      style={{
                        color: "white",
                        fontSize: '1.2em',
                        fontWeight: '400',
                        textDecoration:
                          props.location.pathname === navItem[1]
                            ? "underline"
                            : "none"
                      }}
                    >
                      {navItem[0]}
                    </p>
                  </ListItem>
                </Link>
              )}
            </div>
          ))}
        </List>
      </div>
    </Drawer>
  );
};

export default withRouter(SideNavBar);
