import React from "react";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import { createMuiTheme, withTheme } from '@material-ui/core/styles';
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import TextField from "@material-ui/core/TextField/TextField";
import Divider from "@material-ui/core/Divider";

const theme = createMuiTheme();

class WithdrawDialog extends React.Component {
  state = {
    open: false,
    passphraseInput: ""
  };

  updatePassphraseInput = event => {
    this.setState({ passphraseInput: event.target.value });
  };

  retrieveFunds = feeless => () => {
    if (feeless) {
      this.props.retrieveFundsFeeless(
        this.props.payment.paymentId,
        this.state.passphraseInput,
        this.props.payment.sender
      );
    } else {
      this.props.retrieveFunds(
        this.props.payment.paymentId,
        this.state.passphraseInput
      );
    }
    this.props.toggleWithdraw(false)();
  };

  isPassphraseCorrect = passphrase => {
    return (
      this.props.payment.codeHash ===
      window.web3.utils.soliditySha3(passphrase, this.props.payment.recipient)
    );
  };

  render() {
    const passphraseCorrect = this.isPassphraseCorrect(
      this.state.passphraseInput
    );
    const classes = this.props;
    const key = this.props._key;
    return (
      <div>
        <Dialog
          open={this.props.open}
          onClose={this.props.toggleWithdraw(false)}
          aria-labelledby={"alert-dialog-title" + key}
          aria-describedby={"alert-dialog-description" + key}
        >
          <DialogTitle id={"alert-dialog-title" + key}>
            {"Enter passphrase for payment " +
              this.props.payment.paymentId.substring(0, 12) +
              "..."}
          </DialogTitle>
          <DialogContent>
            <DialogContentText id={"alert-dialog-description" + key}>
              Enter the passphrase that has been sent by the recipient for this
              payment:
            </DialogContentText>
            <TextField
              id="outlined-multiline-static"
              className={classes.textField}
              onChange={this.updatePassphraseInput}
              value={this.state.passphraseInput}
              disabled={false}
              margin="normal"
              label={"Passphrase"}
              variant="outlined"
              helperText={
                passphraseCorrect
                  ? "Passphrase is correct!"
                  : "Error: Wrong passphrase"
              }
              error={!passphraseCorrect}
            />
          </DialogContent>
          <Divider />
          <DialogActions>
            <Button
              onClick={this.retrieveFunds(false)}
              disabled={!passphraseCorrect}
              color="primary"
            >
              Submit
            </Button>
            <Button
              onClick={this.retrieveFunds(true)}
              disabled={!passphraseCorrect}
              color="primary"
            >
              Submit (feeless)
            </Button>
            <Button
              onClick={this.props.toggleWithdraw(false)}
              color="primary"
              autoFocus
            >
              Cancel
            </Button>
          </DialogActions>
        </Dialog>
      </div>
    );
  }
}

export default withTheme((WithdrawDialog));
