import React from "react";
import { Button, Divider, Grid, TextField } from "@material-ui/core";
import lightGreen from "@material-ui/core/colors/lightGreen";
import red from "@material-ui/core/colors/red";
import { withBreakpoints } from "react-breakpoints";
import { withRouter } from "react-router-dom";
import { createMuiTheme, withStyles } from "@material-ui/core/styles";
import OpenInNew from "@material-ui/icons/OpenInNew";
import IconButton from "@material-ui/core/IconButton/IconButton";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import App from "./App";
import CancelPaymentDialog from "./CancelPaymentDialog";
import WithdrawDialog from "./WithdrawDialog";

// custom mui theme
const theme = createMuiTheme();

const styles = theme => ({
  buttonLabel: {
    color: "white"
  },
  button: {
    color: theme.palette.secondary.main
  }
});

// Returns true if to and from address are equal
const addrEquals = (str1, str2) => {
  try {
    return str1.toLowerCase() === str2.toLowerCase();
  } catch (err) {
    return str1 === str2;
  }
};

/**
 * PaymentDetails is the component that renders
 * information and action buttons for specific payment
 */

class PaymentDetails extends React.Component {
  state = {
    openCancel: false,
    openWithdraw: false,
    recipientName: null,
    senderName: null
  };

  toggleWithdraw = bool => () => {
    this.setState({ openWithdraw: bool });
  };

  toggleCancel = bool => () => {
    this.setState({ openCancel: bool });
  };

  fetchENSNames = async txn => {
    let recipientName = await this.props.getCachedAddrForENSName(txn.recipient);
    let senderName = await this.props.getCachedAddrForENSName(txn.sender);
    this.setState({ recipientName, senderName });
  };

  render() {
    // returns whether payment is OPEN, CLOSED or CANCELLED
    const PaymentStatus = { OPEN: 0, CLOSED: 1, CANCELLED: 2 };
    // need this for breakpoints
    const { breakpoints, currentBreakpoint } = this.props;

    // used t map payment status to payment.state
    const PaymentStatusToName = {
      0: "OPEN",
      1: "COMPLETED",
      2: "CANCELLED"
    };

    const handleBackButton = () => this.props.history.push("/app/payments");

    const account =
      this.props.allAccounts === undefined || this.props.allAccounts === []
        ? 0
        : this.props.allAccounts[0];

    const txn = this.props.payment;

    const openDate =
      txn.paymentOpenTime === 0
        ? undefined
        : new Date(txn.paymentOpenTime * 1000);
    const closeDate =
      txn.paymentCloseTime === 0 || txn.paymentCloseTime === undefined
        ? undefined
        : new Date(txn.paymentCloseTime * 1000);

    const shortAddr = (string, num) => {
      return string.substring(0, num) + "...";
    };

    const fromOrTo = (() => {
      if (addrEquals(account, txn.recipient)) {
        if (addrEquals(account, txn.sender)) {
          return "SELF PAYMENT";
        } else {
          return "FROM: " + shortAddr(txn.sender, 12);
        }
      } else {
        return "TO: " + shortAddr(txn.recipient, 12);
      }
    })();

    const status = txn.state;

    // TODO: should be latest block TS.
    const currTs = Date.now();

    if (this.state.recipientName === null || this.state.senderName === null) {
      this.fetchENSNames(txn);
    }

    let recipientName =
      this.state.recipientName === undefined
        ? txn.recipient
        : this.state.recipientName;
    let senderName =
      this.state.senderName === undefined ? txn.sender : this.state.senderName;

    const renderRecipientActions = (txn, timeLeft) => {
      return (
        <React.Fragment>
          <Button
            variant="contained"
            color="primary"
            disabled={status !== PaymentStatus.OPEN || timeLeft === 0}
            size="small"
            onClick={this.toggleWithdraw(true)}
          >
            Withdraw Funds
          </Button>
          <WithdrawDialog
            _key={this.props._key}
            toggleWithdraw={this.toggleWithdraw}
            open={this.state.openWithdraw}
            retrieveFunds={this.props.retrieveFunds}
            retrieveFundsFeeless={this.props.retrieveFundsFeeless}
            payment={txn}
          />
        </React.Fragment>
      );
    };

    const renderSenderActions = (txn, timeLeft) => {
      const { classes } = this.props;
      return (
        <React.Fragment>
          <Button
            variant="contained"
            color="secondary"
            className={classes.button}
            classes={{
              label: classes.buttonLabel
            }}
            disabled={status !== PaymentStatus.OPEN}
            size="small"
            onClick={this.toggleCancel(true)}
          >
            Cancel Payment
          </Button>
          <CancelPaymentDialog
            _key={this.props._key}
            toggleCancel={this.toggleCancel}
            open={this.state.openCancel}
            cancelPayment={this.props.cancelPayment}
            cancelPaymentFeeless={this.props.cancelPaymentFeeless}
            payment={txn}
          />
        </React.Fragment>
      );
    };

    // TODO: defined twice.
    const formatTimeJSON = jsonStr => {
      return (
        "Days: " +
        jsonStr["days"] +
        ", Hours: " +
        jsonStr["hours"] +
        ", Minutes: " +
        jsonStr["minutes"]
      );
    };

    let utils = window.web3.utils;

    // TODO: this logic is repeated in PaymentsIndex
    let symbol = this.props.tokenAddrToSymbol()[txn.token.toLowerCase()];
    let numDecimals = 18;

    if (symbol !== "ETH") {
      // TODO: this happens when we don't have the network, and
      // return an empty tokenMap. This is probably not the
      // best way to handle it, since it's a silent failure
      // and it will be left as the default.
      if (this.props.getTokenMap()[symbol] !== undefined) {
        numDecimals = parseInt(this.props.getTokenMap()[symbol].decimals);
      }
    }

    let amount =
      symbol === "ETH"
        ? utils.fromWei(txn.sendAmount.toString(), "ether")
        : txn.sendAmount / Math.pow(10, numDecimals);

    // TODO: need to use block timestamp here, unless accuracy +/- 15 min is not needed.
    let timeLeft =
      txn.paymentOpenTime * 1000 + txn.holdTime * 1000 > currTs
        ? 1000 * txn.paymentOpenTime + 1000 * txn.holdTime - currTs
        : 0;

    const bodyWidth = window.screen.width < 1200 ? "75%" : "100%";

    // changes color of payment status
    // depending on whether it is open, closed or cancelled
    let statusColor =
      PaymentStatusToName[status] === "OPEN" ? lightGreen[500] : red[800];

    // helps the flex-box be more mobile
    // responsive
    const flexLayout = window.screen.width < 1200 ? "column-reverse" : "row";

    const dynamicFont = window.screen.width < 1200 ? ".70em" : "1em";

    const dateOptions = {
      year: "numeric",
      month: "short",
      day: "numeric",
      minute: "numeric",
      hour: "numeric"
    };

    const toLocaleString = date => {
      return date.toLocaleString("en", dateOptions);
    };

    return (
      <div
        style={{
          padding: "40px",
          width:
            breakpoints[currentBreakpoint] < breakpoints.desktop
              ? "75%"
              : "90%",
          marginLeft: "32px"
        }}
      >
        <Grid
          container
          direction="row"
          justify="flex-start"
          alignItems="center"
        >
          <IconButton onClick={() => handleBackButton()}>
            <ArrowBackIcon fontSize="large" color="primary" />
          </IconButton>
        </Grid>

        <Grid
          container
          direction="column"
          justify="center"
          alignContent="center"
        >
          <h3
            style={{
              fontSize: "2em",
              textAlign: "center",
              fontWeight: "500",
              marginBottom: "0px"
            }}
          >
            Payment Details
          </h3>
          <p
            style={{
              marginTop: "4px",
              marginBottom: "0px",
              textAlign: "center"
            }}
          >
            {fromOrTo}
          </p>
          <p
            style={{
              marginTop: "2px",
              marginBottom: "4px",
              textAlign: "center",
              fontSize: ".75em"
            }}
          >
            {toLocaleString(openDate)}
          </p>
          <Divider variant="fullWidth" />
        </Grid>
        <Grid container direction={flexLayout} justify="space-between">
          <div>
            <div>
              <div style={{ fontSize: dynamicFont }}>
                <Grid container direction="column" justify="space-evenly">
                  <p>
                    <strong>Payment ID:</strong>{" "}
                    {shortAddr(
                      window.web3.utils.toBN(txn.paymentId).toString(),
                      12
                    )}
                  </p>
                  <p>
                    <strong>From:</strong> {senderName}
                  </p>
                  <p>
                    <strong>To:</strong> {recipientName}
                  </p>
                  <Grid container direction="row">
                    <p style={{ marginRight: "8px" }}>
                      <strong> Amount:</strong> {amount}
                    </p>
                    <p>
                      <strong> Token:</strong> {symbol}
                    </p>
                  </Grid>
                  <p>
                    <strong>Time opened:</strong> {toLocaleString(openDate)}
                  </p>
                  <p>
                    <strong>Time closed:</strong>{" "}
                    {closeDate === undefined
                      ? "N/A"
                      : toLocaleString(closeDate)}
                  </p>
                  <p>
                    <strong> Holding period:</strong>{" "}
                    {formatTimeJSON(millisToJSON(txn.holdTime * 1000))}
                  </p>
                </Grid>
                <br />
                <h3 style={{ textDecoration: "underline", fontSize: "2em" }}>
                  {" "}
                  Transaction Hashes:{" "}
                </h3>
                <br />
                <div>
                  <Grid container direction="row" alignContent="center">
                    <p style={{ marginRight: "16px", marginTop: "16px" }}>
                      <strong>Payment opened:</strong>
                    </p>
                    <IconButton
                      href={this.props.getEtherscanTxnURL(txn.openHash)}
                      target={"_blank"}
                    >
                      <OpenInNew />
                    </IconButton>
                  </Grid>
                  <TextField
                    id={
                      "outlined-multiline-static-hash-0-" +
                      this.props.uniqueKey +
                      "-" +
                      this.props._key
                    }
                    style={{ marginLeft: "16px" }}
                    value={txn.openHash}
                    disabled
                  />
                </div>
                {status === PaymentStatus.OPEN ? (
                  ""
                ) : (
                  <div style={{ width: "50%" }}>
                    <Grid container direction="row">
                      <p style={{ marginRight: "16px", marginTop: "16px" }}>
                        <strong>Payment closed:</strong>
                      </p>
                      <IconButton
                        href={this.props.getEtherscanTxnURL(txn.closeHash)}
                        size="small"
                        target={"_blank"}
                      >
                        <OpenInNew />
                      </IconButton>
                    </Grid>
                    <TextField
                      id={
                        "outlined-multiline-static-hash-1-" +
                        this.props.uniqueKey +
                        "-" +
                        this.props._key
                      }
                      value={txn.closeHash}
                      style={{ marginLeft: "16px" }}
                      disabled
                      margin="normal"
                      label="Transaction hash"
                    />
                  </div>
                )}
                <br />
                {status !== PaymentStatus.OPEN ? (
                  <React.Fragment>
                    <p>This payment is closed.</p>
                  </React.Fragment>
                ) : (
                  ""
                )}
                {timeLeft === 0 ? (
                  <React.Fragment>
                    <p style={{ marginLeft: "80px" }}>
                      The time window to complete payment has closed.
                    </p>
                  </React.Fragment>
                ) : (
                  ""
                )}
              </div>
            </div>
          </div>
          <div style={{ margin: "auto", marginTop: "16px" }}>
            <h3
              style={{
                fontSize: "2.5em",
                textAlign: "center",
                color: statusColor,
                fontWeight: "bold",
                marginBottom: "0px"
              }}
            >
              {PaymentStatusToName[status]}
            </h3>
            <p style={{ fontSize: "0.75em", marginTop: "2px" }}>
              Time left: {formatTimeJSON(millisToJSON(timeLeft))}
            </p>
          </div>
        </Grid>
        <Divider />
        <div style={{ width: "100%", marginLeft: "40px" }}>
          <Grid container direction="row" justify="space-between">
            {addrEquals(account, txn.recipient)
              ? renderRecipientActions(txn, timeLeft)
              : ""}
            {addrEquals(account, txn.sender)
              ? renderSenderActions(txn, timeLeft)
              : ""}
          </Grid>
        </div>
      </div>
    );
  }
}

function millisToJSON(ms) {
  let d, h, m, s;
  s = Math.floor(ms / 1000);
  m = Math.floor(s / 60);
  s = s % 60;
  h = Math.floor(m / 60);
  m = m % 60;
  d = Math.floor(h / 24);
  h = h % 24;
  return { days: d, hours: h, minutes: m, seconds: s };
}

export default withStyles(styles, { withTheme: theme })(
  withBreakpoints(withRouter(PaymentDetails))
);
